.cropper {
    position: relative;
    width: 100%;
    height: 60vh;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    border-radius: 8px;
    overflow: hidden;
}

.input-contact{
    color: #232323 !important;
}

.icon-transition{
    transition: ease .4s;
}

.form-input-field{
    margin-top: 10px !important;
}

.form-submit-btn{
    margin-top: 30px !important;
}

.third-theme .primaryaction-button{
    background-color: #232323;
    color: #FFF;
    border-radius: 10px !important;
}
